import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  status = false;
  btnText = "mute";

 
 myFunction() {
	  if (this.status == true) {
	    this.status = true;
	    this.btnText = "Un mute";
	  } else {
	    this.status = false;
	    this.btnText = "mute";
	  }
    }
}
